import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import { Friends, Account, Users } from './api';

import Home from './panels/Home';
import VueFriends from './panels/VueFriends';
import VueOutgoingRequests from './panels/VueOutgoingRequests';

//https://oauth.vk.com/authorize?client_id=6076877&display=page&redirect_uri=&scope=likes+notifications+post+wall+friends+status&response_type=token&v=&state=

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			access_token: 'b726967364a5652844d2f493da0e0ed1c90f0612299a057f1d80ee544388d15ea6eff6ff4f1166431f9fd',
		};
	}

	componentDidMount() {
		this.getUserInfo();
	}
	getUserInfo () {
		Users.getUsers({
			fields: 'photo_200,status,city',
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState({ fetchedUser: data.response[0] });
		})
		.catch(e => console.error(e));
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to });
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} access_token={this.state.access_token} />
				<VueFriends
					id="friends"
					go={this.go}
					access_token={this.state.access_token}
				/>
				<VueOutgoingRequests
					id="subscribers"
					go={this.go}
					access_token={this.state.access_token}
				/>
			</View>
		);
	}
}

export default App;
